import { Component } from '@angular/core';
import { NavController } from '@ionic/angular';
import { DataServiceService } from '../services/data-service.service';

@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {
  results:string;
  searchTerm = '';

  constructor(private dades: DataServiceService, private navCtrl: NavController) {}

  searchEventFired() {
    if(this.dades.dadesPais(this.searchTerm)){
      this.dades.dadesPais(this.searchTerm).subscribe(
        (data: any) => {
          if(data){
            this.results = data[0].Country;
          }
        }
      );
    }
  }

  infoPais(pais:string){
    const titleEscaped = encodeURIComponent(pais);
    this.navCtrl.navigateForward(`/pais/`+titleEscaped);
  }
}
